import { clsx } from 'clsx';
import { HeaderShortProps } from './headershort.interface';

export const HeaderShort = ({ textColor, tagline, headline, showArrow }: HeaderShortProps) => {
  const containerClass =
    textColor === 'white'
      ? '!zep-text-typography-light-100'
      : textColor === 'black'
      ? '!zep-text-typography-dark-100'
      : 'zep-text-indigo-500';

  const taglineClass =
    textColor === 'white'
      ? '!zep-text-steel-500'
      : textColor === 'black'
      ? 'zep-text-typography-dark-70/70'
      : 'zep-text-steel-500';
  return (
    <div
      className={clsx(
        'zep-container',
        'zep-px-1.5',
        'sm:zep-px-1',
        'zep-grid',
        'zep-gap-gridGap-md',
        'lg:zep-gap-gridGap-lg',
        'md:zep-grid-cols-16',
        'zep-mx-auto',
      )}
      data-testid="header-short"
    >
      <div className={clsx('zep-grid-cols-1', 'md:zep-col-start-1', 'md:zep-col-span-6')}>
        <div className="zep-flex">
          {showArrow ? (
            <img
              className={clsx(
                'zep-h-4',
                'sm:zep-h-[100px]',
                'md:zep-h-4',
                'xl:zep-h-[100px]',
                'zep-w-auto',
                'zep-mr-1.5',
                'sm:zep-mr-2.5',
                'md:zep-mr-1.5',
                'xl:zep-mr-3.5',
                'zep-self-center',
              )}
              alt="forward arrow"
              src="./assets/moving-forward-arrow.svg"
            ></img>
          ) : null}

          <div className="zep-block zep-self-center">
            <h3 className={clsx(taglineClass, 'zep-mb-0.5 zep-typography-taglineMD zep-text-steel-500 zep-block')}>
              {tagline}
            </h3>
            <h2 className={clsx(containerClass, 'zep-typography-headlineLG-fluid-cqi zep-block')}>{headline}</h2>
          </div>
        </div>
      </div>
    </div>
  );
};
